<template>
  <div class="findWithdrawPassword">
    <el-form 
      class="findWithdrawPassword__form" 
      ref="forget" 
      :model="forget"
      :rules="rules" 
      label-width="80px">
      <el-form-item label="手机号 :" prop="phone">
        <el-input class="findWithdrawPassword_input" size="mini" placeholder="请输入手机号" v-model="forget.phone">
          <el-button :disabled="timerFlag" class="findWithdrawPassword__code" slot="append" @click="getVerificationCode">
            {{ num }}
          </el-button>
        </el-input>
      </el-form-item>
      <el-form-item label="验证码 :" prop="code">
        <el-input class="findWithdrawPassword_input" size="mini" v-model="forget.code"></el-input>
      </el-form-item>
      <el-form-item label="密码 :" prop="password">
        <el-input class="findWithdrawPassword_input" size="mini" v-model="forget.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="mini" type="primary" @click="onSubmit">修改支付密码</el-button>
        <el-button size="mini" type="success" @click="$router.push({ name: 'withdraw' })">返回提現</el-button>
      </el-form-item>
    </el-form>    
  </div>
</template>

<script>
import { findPayPassword, getVerificationCode } from "@/api/common.js";

export default {
  name: "FindWithdrawPassword",
  data() {
    return {
      forget: {
        phone: "",
        email: "",
        type: "sms",
        password: "",
        code: ""
      },
      rules: {
        phone: [
           { required: true, message: '请输入手机号', trigger: 'blur' }
        ],
        password: [
           { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        code: [
           { required: true, message: '请输入验证码', trigger: 'blur' }
        ]
      },
      num: "获取验证码",
      timerFlag: false
    }
  },
  methods: {
    getVerificationCode() {
      getVerificationCode(this.forget.phone).then(() => {
        this.$message.success("发送成功!");
        this.timerFlag = true;
        let time = 60;
        let timer = setInterval(() => {
          if(time > 0) {
            time = time - 1;
            this.num = time + " s";
          }else {
            this.num = "获取验证码";
            this.timerFlag = false;
            clearInterval(timer);
          }
        }, 1e3);
      }).catch((data) => {
        this.$message.error(data.response.data.message);
      })
    },
    onSubmit() {
      this.$refs['forget'].validate((valid) => {
        if (valid) {
          findPayPassword(this.forget).then(() => {
            this.$message.success("修改成功!");
            this.reset();
            this.$router.push({ name: "withdraw" });
          }).catch(data => {
            this.$message.error(data.response.data.message);
          })
        }
      });
    },
    reset() {
      this.$refs['forget'].resetFields();
    }
  }
}
</script>

<style lang="scss">
$prefix: findWithdrawPassword;

.#{$prefix} {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  background: #EBEEF5;
  &__form {
    width: 40%;
  }
  &__input {
    width: 350px;
  }
  &__code {
    color: #00a1d6;
    cursor: pointer;
  }
}
</style>